<script>
import { mainStore } from "../store/mainStore";
import { ref, computed, reactive, onMounted } from "vue";
import { storeToRefs } from 'pinia';
export default {
	name: "SubscriptionPage",
	data() {
		return {
			conecto:false,
		};
	},
	computed: {
		estaConectado() {
			return this.conecto;
		},
	},
	setup() {
          const store = mainStore();
          const updating = ref(false);

          const updateFragments = ()=>{
               store.updateNFTsStakingBalances();
               console.log('updateFragments')
          };


          const addTToMetamask = async () => {

             const tokenSymbol = 'TMINE';
             const tokenDecimals = 18;
             const tokenImage = 'https://raw.githubusercontent.com/topaciotrade/topaciotrade/main/docs/_media/Tmine.svg';

             console.log('addTToMetamask')

             try {

               const configToken = {
                    type: 'ERC20',
                    options:{
                         symbol:'TMINE',
                         decimals:18,
                         image:tokenImage,
                         address:'0x9D44366dD501dc7010902Dd4293ba83E5da01301'
                    }
               };

               const wasAdded = await store.provider.send('wallet_watchAsset',configToken
                    );


                 if (wasAdded) {
                     console.log('Thanks for your interest!');
                 } else {
                     console.log('Your loss!');
                 }
             } catch (error) {
                 console.log(error);
             }
         }

          const getUrlIPFS = (meta)=>{
               const metaObj = JSON.parse(meta);
               if(!metaObj){ 
                    return `https://mir-s3-cdn-cf.behance.net/project_modules/max_1200/4aae0b77659967.5c8e7351a61f4.jpg`;
               }
               return metaObj.image;
          };

          const getNameFragment = (meta)=>{
               const metaObj = JSON.parse(meta);
               if(!metaObj){ 
                    return ``;
               }
               return metaObj.name;
          };

          const getAtributesFragment = (meta)=>{
               const metaObj = JSON.parse(meta);

               if(!metaObj||!metaObj.attributes){
                    return ``;
               }


               const cadena = metaObj.attributes.flatMap( (v,i) => `${v.trait_type}:${v.value}\n` ).toString().replaceAll(',','');

               return `${metaObj.type}\n${cadena}`;
          };
          

          const refresh = async()=>{
               updating.value = true;
               console.log( updating, 'updating...' );
               await store.searchWallet();
               await store.searchNfts();
               updating.value = false;
          };

          const networdId = ref(null)
          

          store.getNetwork().then((chainId)=>{
               networdId.value = chainId;
          });

		return { 
               store, 
               updating, 
               refresh, 
               networdId, 
               getUrlIPFS, 
               getNameFragment, 
               getAtributesFragment, 
               updateFragments
          };
	},
     onMounted(){       
       console.log('onMounted')
     }
}
</script>

