<template>
	
	<div class="price-selected">
		<input class="price-order" type="number" autocomplete="off" placeholder="price order" v-model="priceOrder" :step="step">
	</div>

	<div class="container-actions-btns">
		<button class="btn-action btn-buy-pushable">
			<span class="btn-buy-shadow"></span>
			<span class="btn-buy-edge"></span>
			<span class="btn-buy-front text"> BUY </span>
		</button>

		<button class="btn-action btn-sell-pushable" role="button">
			<span class="btn-sell-shadow"></span>
			<span class="btn-sell-edge"></span>
			<span class="btn-sell-front text"> SELL </span>
		</button>
	</div>
</template>
<script>
	import {
	defineComponent,
	ref,
	reactive,
	onMounted,
	watch,
	nextTick,
	onUnmounted,
} from "vue";

import { mainStore } from "../store/mainStore";

export default {
	name: "BtnsAcctionsComponets",
	setup() {
		const store = mainStore();

		const priceOrder = ref(0);
		const step = ref(1);


		// atento al evento cuando cambia desde el panel de market de futuros
		watch(
			() => store.getPriceOrder,
			(v) => {
				console.log("getPriceOrder changed detected", v);
				const valor = parseFloat(v);
				if (valor > 100){
					step.value = 1
				}else if(valor > 1){
					step.value = 0.1					
				}else if(valor > 0.1 ){
					step.value = 0.01					
				}else if(valor < 0.1 ){
					step.value = parseFloat(valor/100);
				}

				priceOrder.value = valor;
			}
		);


		
		return { priceOrder, store, step }
	}
};
</script>
