<template>
	<div
		class="relative h-[80px] w-full flex justify-between px-[20px] md:px-[64px] py-[20px] z-[100] bg-opacity"
	>
		<!-- Top Navigation Menu -->
		<div class="topnav" v-if="isMobil">
			<i @click="clickMenu" class="fa fa-bars active"></i>
			<!-- Navigation links (hidden by default) -->
			<div id="myLinks" :class="{ showMenu: menuOpen }">
				<router-link to="/" class="option-menu" @click="menuOpen=false">Home</router-link>
				<router-link to="/team" class="option-menu" @click="menuOpen=false">Team</router-link>
				<router-link
					to="/subscription"
					:class="{
						'option-menu': true,
						'btn-disabled': !isConected || !store.conexionOk,
					}"
					@click="menuOpen=false"
					>Subscription</router-link
				>
				<router-link
					to="/chart"
					:class="{
						'option-menu': true,
						'btn-disabled': !isConected,
					}"
					@click="menuOpen=false"
					>Alpha minimal</router-link
				>

				<router-link
					to="/event"
					:class="{
						'option-menu': true,
						'option-event': true,
					}"
					v-if="isConected && store.conexionOk"
					@click="menuOpen=false"
					>Event</router-link>
			</div>
		</div>

		<div class="w-[160px] sm:w-full flex items-center" v-if="!isMobil">
			<a href="/"
				><div class="text-[#1EEFA4] hidden md:block">
					<!-- <img src="/img/topacio-tecnologi-horizontal.svg" width="220"> -->
					<img src="/img/img-only-text.svg" width="159" />
				</div>
				<div
					class="text-[#1EEFA4] block md:hidden container-btn-rediccion"
				>
					<img src="/img/T.png" width="25" /></div
			></a>

			<router-link to="/" class="option-menu">Home</router-link>

			{{ " " }}
			<router-link to="/team" class="option-menu">Team</router-link>
			{{ " " }}
			<!-- (!isConected) -->
			<router-link
				to="/subscription"
				:class="{
					'option-menu': true,
					'btn-disabled': !isConected || !store.conexionOk,
				}"
				>Subscription</router-link
			>


				<router-link
					to="/event"
					:class="{
						'option-menu': true,
						'option-event': true,
					}"
					v-if="isConected && store.inWhiteList()"					
					>Event</router-link>


			<!-- <a href="#" class="option-menu btn-disabled">Pre-register</a> && store.conexionOk-->
			<router-link
				to="/chart"
				:class="{ 'option-menu': true, 'btn-disabled': !isConected }"
				v-if="isConected"
				>Alpha minimal</router-link
			>
		</div>
		<div class="flex items-center space-x-2 xs:space-x-4 text-white">
			<button
				v-if="!isConected"
				type="button"
				class="flex items-center h-full text-[#134757] md:text-white bg-[#1EEFA4] md:bg-[rgba(14,165,156,0.5)] text-white font-semibold px-5 xs:px-[36px] rounded-full btn-custom-1"
				@click="conectar"
			>
				Connect
			</button>
			<label
				v-if="isConected"
				class="text-wallet-multicolor polygon-colors-gradient"
			>
				<!-- <img src="/img/binance.svg" alt="Binance Logo" class="icon-binance"> -->
				<img
					src="/img/polygon-matic-logo-perspectiva.svg"
					alt="polygon matic Logo"
					class="icon-polygon"
				/>

				{{ maskWallet }}
			</label>
		</div>
	</div>
</template>
<script>
import { mainStore } from "../store/mainStore";
import useUtils from "../composables/useUtils";
import { ref, computed } from "vue";
const { checkMetamask } = useUtils();

export default {
	name: "MenuWeb",
	data() {
		return {
			conecto: false,
		};
	},
	computed: {
		estaConectado() {
			return this.conecto;
		},
	},
	setup() {
		const store = mainStore();
		const w = ref("");
		const menuOpen = ref(false);
		const vw = ref(
			Math.max(
				document.documentElement.clientWidth || 0,
				window.innerWidth || 0
			)
		);
		const vh = ref(
			Math.max(
				document.documentElement.clientHeight || 0,
				window.innerHeight || 0
			)
		);

		window.addEventListener("resize", (e) => {
			console.log(`width: ${e.target.visualViewport.width}px`);
			console.log(`height: ${e.target.visualViewport.height}px`);

			vw.value = e.target.visualViewport.width;
			vh.value = e.target.visualViewport.height;

			store.setVW(e.target.visualViewport.width);
			store.setVH(e.target.visualViewport.height);

		});

		const isMobil = computed(() => {
			return vw.value < 1000;
		});

		const conectar = async () => {
			console.log("conectar...");
			const respConexionBlockchain = await store.isCorrectBlockchain();
			console.log("isCorrectBlockchain:", respConexionBlockchain);
			if (!respConexionBlockchain) {
				await store.changeNetworkSwich();

				// if (checkMetamask()) {
				// 		const r = await store.searchWallet();
				// 		w.value = r;
				// }
			} else {
				//w.value = await store.searchWallet();
				const rw = await store.onlySearchWallet();
				w.value = new String(rw[0]).trim();
				store.setWallet(new String(rw[0]).trim());

				// store.setWallet(await store.searchWallet());
				// w.value = store.wallet.toString();
			}
		};

		const isConected = computed(() => {
			const hasConex =
				w != "" && store && store.wallet && store.wallet != "";
			// console.log({hasConex,sw:store.wallet},hasConex!='')
			if (hasConex && hasConex != "") {
				w.value = store.wallet;
			}

			//return (store && store.wallet && store.wallet!='');
			console.log({ hasConex });
			return hasConex;
			//w != "" && store.wallet!='';
		});

		const maskWallet = computed(() => {
			return w.value && w.value.substr
				? `${w.value.substr(0, 5)}...${w.value.substr(-5, 5)}`
				: "";
		});

		const clickMenu = () => {
			menuOpen.value = !menuOpen.value;
		};

		return {
			conectar,
			isConected,
			maskWallet,
			w,
			store,
			vw,
			vh,
			isMobil,
			menuOpen,
			clickMenu,
		};
	},
};
</script>
