<template>
	<div class="container-futures-markets">
		<div class="contenedor-panel-market-future">
			<ul id="lista-pares">
				<li v-for="par in pares" :id="`li-${par.name}`">
					<label
						:id="`lbl-cryto-${par.name}`"
						class="lbl-cryto"
						@click="changeTargetCrypto(par.name)"
						>{{ par.name }}</label
					>
					<label :id="`lbl-volume-${par.name}`" class="lbl-volume"
						>{{ formatQuote(par.quote) }} M</label
					>
					<label :id="`lbl-close-${par.name}`" class="lbl-close">{{
						par.close
					}}</label>
					<label
						:id="`lbl-porcentaje-${par.name}`"
						class="lbl-porcentaje color-negative"
						>{{ par.porcentage }}%</label
					>
				</li>
			</ul>


<!-- 			<button
				class="btn-close-panel-market"
				v-on:click="ocultarPanelMarket"
			>
				X
			</button> -->
		</div>

		<button class="btn-show" v-on:click="mostrarPanelMarket">
			Binance markets futures
		</button>

		<section class="btns-orden" v-if="mostrandoMarket">
				<button class="btn-filter btn-sort-name" @click="sortBy('name')">
					Name
				</button>
				<button class="btn-filter btn-sort-cap" @click="sortBy('market')">
					Market
				</button>
				<button class="btn-filter btn-sort-price" @click="sortBy('price')">
					Price
				</button>
				<button
					class="btn-filter btn-sort-porcentage"
					@click="sortBy('porcentage')"
				>
					Porcentage
				</button>
		</section>

	</div>
</template>
<script>
import { ref, watch, computed } from "vue";
import useUtils from "../composables/useUtils";
import { mainStore } from "../store/mainStore";
const { porcentageDiff } = useUtils();
export default {
	name: "FuturesMarkets",
	setup() {
		const store = mainStore();
		const pares = ref([]);
		const filter = ref(null);
		const mostrandoMarket = ref(false);

		const socketCrypto24h = computed(() =>
			store && store.allCryptos24h ? store.allCryptos24h : []
		);


		watch(socketCrypto24h, (newValue, oldValue) => {
			// procesar los nuevos valores que va actualizando el socket
			processUpdate24hrMiniTicker(newValue);
		});

		const changeTargetCrypto = (par) => {
			store.setTargetSymbol(par);
			ocultarPanelMarket();
		};

		const processUpdate24hrMiniTicker = (data) => {
			data.map((socketPar) => {
				let parEncontrado = pares.value.find(
					(d) => d.name == socketPar.s
				);

				let openPrice = parseFloat(socketPar.o);
				let closePrice = parseFloat(socketPar.c);
				let volume = parseFloat(socketPar.v);
				//let quote = parseFloat(parseFloat(socketPar.q)/1000000).toFixed(2);
				let porcentaje = porcentageDiff(openPrice, closePrice);
				if (parEncontrado == undefined) {
					// si aun no esta en pares  se agrega
					pares.value.push({
						name: socketPar.s,
						close: closePrice,
						open: openPrice,
						porcentage: porcentaje,
						volume: volume,
						quote: socketPar.q,
					});
				} else {
					let indexPosicionEncontrada = pares.value.findIndex(
						(p) => p.name == socketPar.s
					);
					if (indexPosicionEncontrada != -1) {
							pares.value[indexPosicionEncontrada] = {
								name: socketPar.s,
								close: closePrice,
								open: openPrice,
								porcentage: porcentaje,
								volume: volume,
								quote: socketPar.q,
							};
					}
				}

				let target = document.getElementById(
					`lbl-close-${socketPar.s}`
				);
				let targetPorcentaje = document.getElementById(
					`lbl-porcentaje-${socketPar.s}`
				);

				if (target) {
					target.innerText = socketPar.c;
				}

				if (targetPorcentaje) {
					targetPorcentaje.classList.remove("color-positive");
					targetPorcentaje.classList.remove("color-negative");
					let caseIsNegative = "";
					if (closePrice > openPrice) {
						targetPorcentaje.classList.add("color-positive");
					}
					if (closePrice < openPrice) {
						targetPorcentaje.classList.add("color-negative");
						caseIsNegative = "-";
					}

					targetPorcentaje.innerText = `${caseIsNegative}${parseFloat(
						porcentaje
					).toFixed(2)}%`;
					//color-positive
				}
			});
		};

		const sortBy = (filtro) => {
			filter.value = filtro;
			let filtrado = [];

			if (filtro == "name") {
				let filtrado = pares.value.sort((a, b) => {
					const aM = a.name.toLowerCase();
					const bM = b.name.toLowerCase();
					if (aM < bM) {
						return -1;
					}
					if (aM > bM) {
						return 1;
					}
					return 0;
				});
				pares.value = filtrado;
			}

			if (filtro == "porcentage") {
				let filtrado = pares.value.sort(ordenarByPorcentage);
				pares.value = filtrado;
			}
			if (filtro == "price") {
				let filtrado = pares.value.sort(ordenarByPrice);
				pares.value = filtrado;
			}
			if (filtro == "market") {
				let filtrado = pares.value.sort(ordenarByMarket);
				pares.value = filtrado;
			}
		};

		const ordenarByMarket = (x, y) => {
			let xQuote = parseFloat(x.quote);
			let yQuote = parseFloat(y.quote);
			if (xQuote > yQuote) {
				return -1;
			}
			if (xQuote < yQuote) {
				return 1;
			}
			return 0;
		};
	
		const ordenarByPrice = (x, y) => {
			if (x.close > y.close) {
				return -1;
			}
			if (x.close < y.close) {
				return 1;
			}
			return 0;
		};

		const ordenarByPorcentage = (x, y) => {
			if (x.porcentage > y.porcentage) {
				return -1;
			}
			if (x.porcentage < y.porcentage) {
				return 1;
			}
			return 0;
		};

		const mostrarPanelMarket = () => {
			let targetPanel = !document.getElementsByClassName(
				"contenedor-panel-market-future"
			)
				? null
				: document.getElementsByClassName(
						"contenedor-panel-market-future"
				  )[0];

			if (targetPanel && (mostrandoMarket.value == false) ) {
				targetPanel.classList.remove("mostrar");
				targetPanel.classList.remove("ocultar");
				targetPanel.classList.add("mostrar");
				mostrandoMarket.value = true;
			}else{
				ocultarPanelMarket();
			}
		};
		const ocultarPanelMarket = () => {
			let targetPanel = !document.getElementsByClassName(
				"contenedor-panel-market-future"
			)
				? null
				: document.getElementsByClassName(
						"contenedor-panel-market-future"
				  )[0];

			if (targetPanel) {
				targetPanel.classList.remove("mostrar");
				targetPanel.classList.remove("ocultar");
				targetPanel.classList.add("ocultar");
				mostrandoMarket.value = false;
			}
		};

		const formatQuote = (q) => {
			return parseFloat(parseFloat(q) / 1000000).toFixed(2);
		};

		return {
			mostrarPanelMarket,
			ocultarPanelMarket,
			sortBy,
			formatQuote,
			pares,
			filter,
			store,
			socketCrypto24h,
			mostrandoMarket,
			changeTargetCrypto
		};
	},
};
</script>
