<template v-if="isLoaded">
	<div
		class="container-selector"
		v-if="opciones && opciones.length > 0 && opciones[0].label"
	>
		<form>
			<input
				class="chosen-value"
				type="text"
				:value="txtValue"
				:placeholder="porDefault"
				@input="controllerText"
				@click="abrir"
			/>
			<ul :class="{ 'value-list': true, open: open }">
				<li v-for="o in opcionesData.data" @click="clickOption(o)">
					{{ o.label }}
				</li>
			</ul>
		</form>
	</div>
</template>
<script>
import { ref, reactive, onMounted, toRefs, watch } from "vue";
export default {
	name: "Selector",
	props: ["opciones"],
	setup(props, { emit }) {
		const { opciones, lblPorDefecto } = toRefs(props);
		const open = ref(false);
		const txtValue = ref("");
		const porDefault = opciones.value.filter( s => s.selected);

		const isLoaded = ref(false);

		const defaultOption = reactive({ 
			label: (porDefault && porDefault[0] && porDefault[0].label) ? porDefault[0].label:"SYMBOL", 
			value: (porDefault && porDefault[0] && porDefault[0].value) ? porDefault[0].value:""
		 });

		const opcionesData = reactive({ data: [] });

		const abrir = ()=>{
			open.value = (!open.value)?true:false;
		};

		watch(opciones,(v)=>{

			const seleccionada = v.filter( o => o.selected )

			if(seleccionada && seleccionada[0]){
				//console.log('cambio opciones',v,seleccionada)
				txtValue.value = seleccionada[0].label;
			}
		});

		opcionesData.data = opciones.value;

		const controllerText = (evt) => {
			const txt = evt.target.value.toUpperCase();
			txtValue.value = txt;
			defaultOption.label = "SYMBOL";
			opcionesData.data = props.opciones.filter((o) =>
				o.label.includes(txt)
			);
			open.value = true;
		};

		const clickOption = (o) => {
			open.value = false;
			defaultOption.label = o.label;
			defaultOption.value = o.value;
			txtValue.value = o.value;
			console.log(o.value,'o.value')
			if(!o || !o.value ){ return; }
			setTimeout(emit("actionChange", o),150);
		};

		if(porDefault && porDefault[0]){
			defaultOption.value = porDefault[0].value;
			defaultOption.label = porDefault[0].label;
			txtValue.value = porDefault[0].label;
			//console.log({d:porDefault[0].label,defaultOption})
		}

		onMounted(async () => {
			if (props.opciones && props.opciones[0] != undefined && !porDefault[0]) {
				defaultOption.label = props.opciones[0].label;
				defaultOption.value = props.opciones[0].value;
				txtValue.value = props.opciones[0].value;
				isLoaded.value = true;
			}else{
				isLoaded.value = false;
			}
		});

		return {
			open,
			controllerText,
			clickOption,
			defaultOption,
			txtValue,
			opcionesData,
			porDefault,
			abrir
		};
	},
};
</script>
<style scoped lang="scss">
@import "../scss/selector.scss";
</style>

<style scoped>
*,
*::before,
*::after {
	box-sizing: border-box;
}

.container-selector{
   margin: 0.5em 0;
   width: 100%;
   height: 30px;
}

/*the container must be positioned relative:*/
.custom-select {
	position: relative;
	font-family: Arial;
}

.custom-select select {
	display: none; /*hide original SELECT element:*/
}

.select-selected {
	background-color: #c1c1c2;
}

/*style the arrow inside the select element:*/
.select-selected:after {
	position: absolute;
	content: "";
	top: 14px;
	right: 10px;
	width: 0;
	height: 0;
	border: 6px solid transparent;
	border-color: #fff transparent transparent transparent;
}

/*point the arrow upwards when the select box is open (active):*/
.select-selected.select-arrow-active:after {
	border-color: transparent transparent #fff transparent;
	top: 7px;
}

/*style the items (options), including the selected item:*/
.select-items div,
.select-selected {
	color: #ffffff;
	padding: 8px 16px;
	border: 1px solid transparent;
	border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
	cursor: pointer;
	user-select: none;
}

/*style items (options):*/
.select-items {
	position: absolute;
	background-color: DodgerBlue;
	top: 100%;
	left: 0;
	right: 0;
	z-index: 99;
}

/*hide the items when the select box is closed:*/
.select-hide {
	display: none;
}

.select-items div:hover,
.same-as-selected {
	background-color: rgba(0, 0, 0, 0.1);
}
</style>
