		const psarUpdate = () => {
			if (!checkPSAR.value) {
				linePSAR.setData([]);
				return;
			}

			const psarFormat = dataCandles.value.flatMap((d) => {
				let color = d.high > d.psar ? "#19f5eb" : "red";

				if (d.close == d.psar) {
					color = "fff";
				}

				return d.psar
					? {
							value: d.psar,
							time: d.time,
							color,
					  }
					: [];
			});

			linePSAR.setData(psarFormat);
		};

		const atrUpdate = () => {
			if (!checkATR.value) {
				lineATR.setData([]);
				lineATRLOW.setData([]);
				return;
			}
			const atrLowValues = dataCandles.value.flatMap((d) => {
				return d.atr
					? {
							value: d.low - d.atr,
							time: d.time,
					  }
					: [];
			});

			const atrHighValues = dataCandles.value.flatMap((d) => {
				return d.atr
					? {
							value: d.high + d.atr,
							time: d.time,
					  }
					: [];
			});

			lineATR.setData(atrHighValues);
			lineATRLOW.setData(atrLowValues);
		};

		const updateChartData = async (velaNueva) => {
