		const procesarData = async (resp) => {
			// validacion asegurando que la
			if (resp instanceof Array && typeof resp.reverse == "function") {
				const openValues = await resp.flatMap((d) => parseFloat(d[1]));
				const highValues = await resp.flatMap((d) => parseFloat(d[2]));
				const lowValues = await resp.flatMap((d) => parseFloat(d[3]));
				const closeValues = await resp.flatMap((d) => parseFloat(d[4]));
				const volumeValues = await resp.flatMap((d) =>
					parseFloat(d[5])
				);

				const psar = calculoPSAR(lowValues, highValues);

				// const datos = {
				// 	period:14,
				// 	multiplier: 2,
				// 	open:openValues,
				// 	high:highValues,
				// 	low:lowValues,
				// 	close:closeValues,
				// 	volume:volumeValues
				// };

				// Chandelier(datos);

				const RSIH = await calculoRSI(highValues, 14);
				const RSIL = await calculoRSI(lowValues, 14);
				const RSI = await calculoRSI(closeValues, 14);

				const BB = calculateBollingerBands(closeValues, 14);
				const BBH = calculateBollingerBands(highValues, 55);
				const BBL = calculateBollingerBands(lowValues, 55);

				let smaData70 = calcularSMA(70, closeValues);
				let smaData50 = calcularSMA(50, closeValues);

				const dataFormatoATR = {
					high: highValues,
					low: lowValues,
					close: closeValues,
					period: 14,
				};

				const calculoATR = calculeATR(dataFormatoATR, 14);

				let reverso = [];
				/* for inverso comienza desde el ultimo */
				for (var i = resp.length - 1; i >= 0; i--) {
					const utimo_RDIH = RSIH.pop();
					const utimo_RDIL = RSIL.pop();
					const utimo_RDI = RSI.pop();
					let utimo_BB = BB.pop();
					const utimo_BBH = BBH.pop();
					const utimo_BBL = BBL.pop();
					const utimo_smaData70 = smaData70.pop();
					const utimo_smaData50 = smaData50.pop();

					const utimo_ATR = parseFloat(
						parseFloat(calculoATR.pop()).toFixed(4)
					);

					// utimo_BB = {
					// 	    "middle": ( utimo_BB && utimo_BB.middle ) ? utimo_BB.middle:null,
					// 	    "upper": ( utimo_BBH && utimo_BBH.upper ) ? utimo_BBH.upper:null,
					// 	    "lower": ( utimo_BBL && utimo_BBL.lower ) ? utimo_BBL.lower:null,
					// 	    "pb": ( utimo_BB && utimo_BB.pb ) ? utimo_BB.pb:null
					// };
					await resp.flatMap((d) => parseFloat(d[4]));

					const bbhl = {
						time: parseInt(resp[i][6]),
						h:
							utimo_BBH && utimo_BBH.upper
								? utimo_BBH.upper
								: null,
						l:
							utimo_BBL && utimo_BBL.lower
								? utimo_BBL.lower
								: null,
					};

					const open = parseFloat(resp[i][1]);
					const high = parseFloat(resp[i][2]);
					const low = parseFloat(resp[i][3]);
					const close = parseFloat(resp[i][4]);

					const porcentajeVela =
						(await calculatePercentageBetweenValues(low, high)) *
						-1;

					const pCompresionPB =
						utimo_smaData50 && utimo_smaData70
							? calculatePercentageBetweenValues(
									utimo_smaData50,
									utimo_smaData70
							  )
							: null;

					const pPricePB =
						utimo_smaData50 && close
							? calculatePercentageBetweenValues(
									utimo_smaData50,
									close
							  )
							: null;
					const pPricePBHigh =
						utimo_smaData50 && high
							? calculatePercentageBetweenValues(
									utimo_smaData50,
									high
							  )
							: null;
					const pPricePBLow =
						utimo_smaData50 && low
							? calculatePercentageBetweenValues(
									utimo_smaData50,
									low
							  )
							: null;

					let structura = {
						time: parseInt(resp[i][6]),
						open,
						high,
						low,
						close,
						volume: parseFloat(resp[i][5]),
						volumeq: parseFloat(resp[i][7]),
						rsih: utimo_RDIH ? utimo_RDIH : null,
						rsil: utimo_RDIL ? utimo_RDIL : null,
						rsi: utimo_RDI ? utimo_RDI : null,
						pv: porcentajeVela,
						bb: utimo_BB ? utimo_BB : null,
						atr: utimo_ATR ? utimo_ATR : null,
						bbhl,
						psar: psar[i] ? psar[i] : null,
						cPB: pCompresionPB,
						pPricePB,
						pPricePBLow,
						pPricePBHigh,
					};

					const rsiMasAlto = Math.max(
						...[utimo_RDIH, utimo_RDIL, utimo_RDI]
					);
					const precioMasAlto = Math.max(...[open, high, low, close]);

					let movmientoCuerpoVela = calculatePercentageBetweenValues(
						open,
						close
					);
					movmientoCuerpoVela =
						movmientoCuerpoVela < 0
							? movmientoCuerpoVela * -1
							: movmientoCuerpoVela;

					let separacionUpperBrutal = false;
					if (utimo_BB && utimo_BB.upper && utimo_BB.upper < high) {
						let separacionPorcentual =
							calculatePercentageBetweenValues(
								utimo_BB.upper,
								high
							) * -1;
						separacionUpperBrutal =
							separacionPorcentual > 3 ? true : false;
					}

					if (
						utimo_RDIH &&
						utimo_BB &&
						(utimo_RDIH >= rsiMax.value || separacionUpperBrutal) &&
						utimo_BB.upper &&
						utimo_BB.upper <= high
					) {
						let color =
							separacionUpperBrutal || high > 90
								? "#ff6000"
								: "orange";
						structura.color = color; // sobre compra
						structura.upColor = color;
						structura.downColor = color;
						structura.borderDownColor = color;
						structura.borderUpColor = color;
						structura.wickDownColor = color;
						structura.wickUpColor = color;
						structura.borderColor = color;
						structura.wickColor = color;
					}

					if (
						utimo_RDIL &&
						utimo_RDIL <= rsiMin.value &&
						utimo_BB &&
						utimo_BB.lower &&
						utimo_BB.lower >= low
					) {
						// sobre venta
						let color = "rgba(25, 245, 235, 1)";
						structura.color = color; // sobre compra
						structura.upColor = color; // *
						structura.downColor = color; // *
						structura.borderDownColor = color;
						structura.borderUpColor = color;
						structura.wickDownColor = color;
						structura.wickUpColor = color;
						structura.borderColor = color;
						structura.wickColor = color;
					}

					// si alcanso valores sobre los 90 RSI y sin cuerpo
					let esSobre90SinCuerpo =
						movmientoCuerpoVela < 0.15 &&
						movmientoCuerpoVela > 0 &&
						rsiMasAlto >= 88;
					let esSobre75SinCuerpoDesbordeBB =
						movmientoCuerpoVela < 0.15 &&
						movmientoCuerpoVela > 0 &&
						rsiMasAlto >= 75 &&
						precioMasAlto > utimo_BB.upper;
					// let esSobre80SinCuerpoDoji = movmientoCuerpoVela<0.10 && movmientoCuerpoVela > 0 && rsiMasAlto >=80;
					if (esSobre90SinCuerpo || esSobre75SinCuerpoDesbordeBB) {
						const color = "#ff00de";
						structura.color = color; // sobre compra
						structura.upColor = color;
						structura.downColor = color;
						structura.borderDownColor = color;
						structura.borderUpColor = color;
						structura.wickDownColor = color;
						structura.wickUpColor = color;
						structura.borderColor = color;
						structura.wickColor = color;
					}

					reverso.push(structura);
				}

				// Ordenando
				// await reverso.reverse();
				let dataOrdenada = [];
				for (var i = reverso.length - 1; i >= 0; i--) {
					dataOrdenada.push(reverso[i]);
				}

				procesarMarcaciones(dataOrdenada);

				return dataOrdenada;
			}
		};

		const procesarMarcaciones = (resposeDataFormat) => {
			/*reset previo*/
			marcaciones.buy = [];
			marcaciones.sell = [];

			resposeDataFormat.map((dt) => {
				const rsiMasAlto = Math.max(...[dt.rsih, dt.rsil, dt.rsi]);
				const rsiMasBajo = Math.min(...[dt.rsih, dt.rsil, dt.rsi]);

				const criterioRSIBuy =
					(temporality.value == "4h" ? 35 : false) ||
					(temporality.value == "15m" ? 30 : false) ||
					(temporality.value == "1h" ? 30 : false) ||
					(temporality.value == "1m" ? 20 : false) ||
					25;

				const criterioRSISell =
					(temporality.value == "1d" ? 75 : false) ||
					(temporality.value == "4h" ? 70 : false) ||
					(temporality.value == "15m" ? 75 : false) ||
					(temporality.value == "1h" ? 75 : false) ||
					(temporality.value == "1m" ? 80 : false) ||
					77;

				const esSuperAgresiva = rsiMasAlto >= 90 ? true : false;

				//si no cumple las condiciones minimas  pasa la vela
				if (
					!(rsiMasBajo <= criterioRSISell) ||
					!rsiMasAlto >= criterioRSISell ||
					!dt.cPB
				) {
					return;
				}

				const compresion = dt.cPB > 0 ? dt.cPB : dt.cPB * -1;

				const porcentajeSeparacionPullbacklow =
					dt.pPricePBLow > 0 ? dt.pPricePBLow : dt.pPricePBLow * -1;

				const porcentajeSeparacionPullbackHigh =
					dt.pPricePBHigh > 0
						? dt.pPricePBHigh
						: dt.pPricePBHigh * -1;

				const precioMasAlto = Math.max(...[dt.high, dt.low, dt.close]);
				const precioMasBajo = Math.min(...[dt.high, dt.low, dt.close]);

				const velaInfo = {
					open: dt.open,
					high: dt.high,
					close: dt.close,
					low: dt.low,
				};

				const velaTime = dt.time;

				const esDoji = isDoji(velaInfo);

				const esBullishspinningtop = isBullishspinningtop(velaInfo);
				const esBearishspinningtop = isBearishspinningtop(velaInfo);

				const porcentajeMovimientoVela =
					calculatePercentageBetweenValues(
						precioMasAlto,
						precioMasBajo
					);
				const porcentajeSeparacionBB =
					precioMasAlto >= dt.bb.upper || precioMasBajo <= dt.bb.lower
						? calculatePercentageBetweenValues(
								precioMasAlto,
								dt.bb.upper
						  )
						: 0;

				const porcentajeSeparacionBBCOMPRA =
					precioMasBajo < dt.bb.lower
						? calculatePercentageBetweenValues(
								precioMasBajo,
								dt.bb.lower
						  ) * -1
						: 0;

				const reglaQuebre =
					dt.rsih > 80 || dt.rsil > 80 || dt.rsi > 80 || false;
				const reglaManupulacion =
					dt.rsih > 80 || dt.rsil > 80 || dt.rsi > 80 || false;

				// comprasBTC -0.5<=-1
				let controlPendienteSeparacionBB = 0.4;
				let controlCriterioCompresion =
					(temporality.value == "1d"
						? rsiMasAlto >= 79
							? 15
							: 5
						: false) ||
					(temporality.value == "4h"
						? rsiMasBajo <= 27 &&
						  porcentajeSeparacionPullbacklow >= 15
							? 2.7
							: 1
						: false) ||
					(temporality.value == "1h" && rsiMasBajo <= 18
						? 1.5
						: false) ||
					(dt.rsi < 80 ? 0.5 : 1) ||
					0.02;

				// ((temporality.value=='4h' && rsiMasBajo<=35 && porcentajeSeparacionPullback > 10 )?2.5:false) ||

				// porcentajes minimos rentables por ir a perseguir
				// si es BTC  no se buscara porcentajes tan grandes
				let multiplicador =
					(dt.close > 10000 ? 1.3 : false) ||
					(dt.close > 500 ? 1.1 : false) ||
					(dt.close > 100 ? 1.2 : false) ||
					(dt.close > 2 ? 2 : false) ||
					(dt.close > 1.2 ? 4 : false) ||
					(dt.close > 0.5 ? 5 : false) ||
					5;

				let controlCriterioSeparacionPBProcentaje =
					(temporality.value == "1d" ? 20 : false) ||
					(temporality.value == "4h" ? 5 : false) ||
					(temporality.value == "15m" ? multiplicador : false) ||
					(temporality.value == "1m" && dt.close <= 1.5
						? 3
						: false) ||
					0.8;

				let criterioRSIDesbordeMovimiento =
					(temporality.value == "4h" ? 4 : false) ||
					(temporality.value == "15m" ? 1 : false) ||
					0.8;

				// dogui
				if (
					esDoji &&
					rsiMasAlto > 70 &&
					(precioMasAlto >= dt.bb.upper ||
						porcentajeSeparacionBB <= 0.25)
				) {
					marcaciones.buy.push({
						time: parseInt(velaTime),
						position: "aboveBar", //"belowBar",
						color: "#19f5eb",
						//size: 2,
						//shape: "arrowUp",
						text: "", //       
						tipo: "bblevel",
					});
				}

				if (
					esBullishspinningtop &&
					rsiMasAlto > 70 &&
					precioMasAlto >= dt.bb.upper &&
					compresion <= controlCriterioCompresion &&
					porcentajeSeparacionPullbackHigh >=
						controlCriterioSeparacionPBProcentaje
				) {
					marcaciones.buy.push({
						time: parseInt(velaTime),
						position: "aboveBar", //"belowBar",
						color: "yelow",
						//size: 2,
						//shape: "arrowUp",
						text: "", //       
						tipo: "bblevel",
					});
				}

				if (
					esBearishspinningtop &&
					rsiMasBajo <= criterioRSIBuy &&
					(precioMasBajo <= dt.bb.lower ||
						porcentajeSeparacionBBCOMPRA <= 0.2) &&
					compresion <= controlCriterioCompresion &&
					porcentajeSeparacionPullbacklow >=
						controlCriterioSeparacionPBProcentaje
				) {
					marcaciones.buy.push({
						time: parseInt(velaTime),
						position: "belowBar", // aboveBar - belowBar
						color: "aquamarine",
						//size: 2,
						//shape: "arrowUp",
						text: "", //       
						tipo: "bblevel",
					});
				}

				// hay una excepcion al RSI solo si esta compreso y hay un trade con mas del 3%
				// y se haya desbordado del bollinger mas del 1.5%
				// COMPRAS
				if (
					dt.bb &&
					dt.bb.lower &&
					(rsiMasBajo <= rsiMin.value ||
						(rsiMasBajo <= 35 &&
							porcentajeSeparacionPullbacklow > 3)) &&
					porcentajeSeparacionBBCOMPRA > 1.5 &&
					precioMasBajo <= dt.bb.lower &&
					compresion <= controlCriterioCompresion &&
					porcentajeSeparacionPullbacklow >=
						controlCriterioSeparacionPBProcentaje
				) {
					// console.log({
					// 	porcentajeSeparacionPullbacklow,
					// });
					/* marcacion de compra */
					marcaciones.buy.push({
						time: parseInt(velaTime),
						position: "belowBar",
						color:
							rsiMasBajo > 30
								? "yellow"
								: "rgba(255, 255, 255, 1)", //"rgba(38, 198, 218, 1)",
						//size: 2,
						//shape: "arrowUp",
						text: "", //"",
						tipo: "bblevel",
					});
				}

				// console.log({
				// 	controlCriterioCompresion,
				// 	controlCriterioSeparacionPBProcentaje
				// })

				if (esSuperAgresiva) {
					/* marcacion de Venta */
					marcaciones.sell.push({
						time: parseInt(velaTime),
						position: "aboveBar",
						color: "yellow", //"#fd1800",
						//size: 2,
						//shape: "arrowUp",
						text: "", //"  ",
					});
				}

				// --------->>
				// VENTAS
				if (
					dt.bb &&
					dt.bb.upper &&
					dt.cPB &&
					(rsiMasAlto >= criterioRSISell ||
						(esDoji && rsiMasAlto > criterioRSISell)) &&
					precioMasAlto >= dt.bb.upper &&
					compresion <= controlCriterioCompresion &&
					porcentajeSeparacionPullbackHigh >=
						controlCriterioSeparacionPBProcentaje
				) {
					// console.log({
					// 	rsiMasAlto,
					// 	controlCriterioSeparacionPBProcentaje,
					// 	porcentajeSeparacionPullbackHigh,
					// 	esDoji
					// })

					const color =
						(porcentajeSeparacionBB > controlPendienteSeparacionBB
							? "#f33"
							: false) || "rgba(255, 255, 255, 1)";

					/*
						: 
							(porcentajeSeparacionBB > controlPendienteSeparacionBB) || esSuperAgresiva
							? "#f33":"rgba(255, 255, 255, 1)", //"rgba(38, 198, 218, 1)"
					*/

					/* marcacion de Venta */
					marcaciones.sell.push({
						time: parseInt(velaTime),
						position: "aboveBar",
						color,
						//size: 2,
						//shape: "arrowUp",
						text: "", //"",
						tipo: "bblevel",
					});
				}
			});

			if (candleSeries && marcaciones.buy && marcaciones.sell) {
				let todas_las_marcaciones = [];
				todas_las_marcaciones = todas_las_marcaciones.concat(
					marcaciones.buy
				);
				todas_las_marcaciones = todas_las_marcaciones.concat(
					marcaciones.sell
				);

				function SortArray(x, y) {
					if (x.time < y.time) {
						return -1;
					}
					if (x.time > y.time) {
						return 1;
					}
					return 0;
				}

				let todasOrdenadas = todas_las_marcaciones.sort(SortArray);

				candleSeries.setMarkers(todasOrdenadas);

				marcarPullback();
				marcarBB();
			}
		};

		const searchData = async () => {
			await openSocket(symbols.value);
			// Busqueda y formateo de data
			const resp = await searchDataChart(
				symbols.value,
				temporality.value,
				700
			);

			datos.value = resp;
			// validacion asegurando que la
			const resposeDataFormat = await procesarData(datos.value);
			dataCandles.value = resposeDataFormat;
			store.setDataCandles(resposeDataFormat);

			return resposeDataFormat;
		};

		const updateChart = async () => {
			const highValues = dataCandles.value.flatMap((d) =>
				parseFloat(d.high)
			);
			const lowValues = dataCandles.value.flatMap((d) =>
				parseFloat(d.low)
			);
			const closeValues = dataCandles.value.flatMap((d) =>
				parseFloat(d.close)
			);

			const vwap = await calcularVWAP(dataCandles.value);
			const RSIH = await calculoRSI(highValues, 14);
			const RSIL = await calculoRSI(lowValues, 14);
			const RSI = await calculoRSI(closeValues, 14);

			let final = dataCandles.value;
			final.RSI = RSI;
			final.RSIH = RSIH;
			final.RSIL = RSIL;

			// dataCandles.value = final;

			candleSeries.setData(dataCandles.value);
			updateVolumen();

			// reset Chart
			chart.timeScale().coordinateToLogical(dataCandles.value.lenght);
		};

		const updateRealtime = (velaSocket, symbol, t) => {
			if (symbol != symbols.value && t != temporality.value) {
				return;
			}

			let auxiliarData = JSON.parse(JSON.stringify(dataCandles.value));
			// actual;izacion con el socket
			auxiliarData[auxiliarData.length - 1].high = velaSocket.high;
			auxiliarData[auxiliarData.length - 1].low = velaSocket.low;
			auxiliarData[auxiliarData.length - 1].close = velaSocket.close;

			const closeValues = auxiliarData.flatMap((v) => v.close);

			calculoRSI(closeValues, 14).then((RSI) => {
				rsiLast.value = parseFloat(RSI.pop()).toFixed(2);
			});
		};

		// atento al evento cuando cambia desde el panel de market de futuros
		watch(
			() => store.getTargetSymbol,
			(v) => {
				console.log("getTargetSymbol changes detected in Chart", v);
				symbols.value = v;
				targetSymbol.value = v;

				dataSymbols.value = store.getSymbols.flatMap((s) => {
					return s.symbol != v
						? { label: s.symbol, value: s.symbol }
						: { label: s.symbol, value: s.symbol, selected: true };
				});

				changeTarget(symbols);
			}
		);

		const openSocket = debounce(async function (socket) {
