			// console.log('socketsLot:',socketsLot.value, socketsLot.value.length,existSocket);

			// await socketsLot.value.map(async (s)=>{
			// 	// removiendo sockets anteriores
			// 	console.log({s});
			// 	const msg = {
			// 		method: "UNSUBSCRIBE",
			// 		params: [`${s.symbol}@kline_${s.temporality}`],
			// 		id: socketsCTR.value,
			// 	};

			// 	await webSocket.value.send(JSON.stringify(msg));
			// 	socketsCTR.value++;
			// });
			// reset anterior data
			socketsLot.value = [];



			if (webSocket.value) {
				await webSocket.value.close();
			}

			if (webSocket24h.value) {
				await webSocket24h.value.close();
			}
			if (allCryptosSocket.value) {
				await allCryptosSocket.value.close();
			}

			const symbolsMinuscula = socket.toString().toLowerCase();

			webSocket.value = await new WebSocket(
				`wss://fstream.binance.com/ws/${symbolsMinuscula}@kline_${temporality.value}`
			);


			webSocket24h.value = await new WebSocket(
				`wss://fstream.binance.com/ws/${symbolsMinuscula}@ticker`
			);

			// Socket ultimas 24h <symbol>@ticker

			allCryptosSocket.value = await new WebSocket(
				`wss://fstream.binance.com/ws/!miniTicker@arr`
			);


			webSocket24h.value.addEventListener("message", function (evt) {

				const dtJson24H = JSON.parse(evt.data);

				// si no se ha recibido el cuerpo o no es el simbolo en target no hase hace nada
				if (
					!dtJson24H ||
					!dtJson24H.s ||
					symbols.value != dtJson24H.s 
				) {
					p24h.value = '';
					return;
				}

				p24h.value = parseFloat(parseFloat(dtJson24H.P).toFixed(2));//`${parseFloat(dtJson24H.P).toFixed(2)} %`
				

				  // "p": "0.0015",      // Price change
				  // "P": "250.00",      // Price change percent
			});

			allCryptosSocket.value.addEventListener("message", function (evt) {
				if (!store) {
					return;
				}
