				const dtJsonCryptos24H = JSON.parse(evt.data);
				debounce(store.setAllCryptos24h(dtJsonCryptos24H), 1000);

				// si aun no hay datos se setean
				// if(previaData.length==0){
				// 	store.setAllCryptos24h(dtJsonCryptos24H);
				// }else{

				// 	// cuando ya hay datos se actualizan solo los nuevos que llegaron
				// 	const symbolos = previaData.flatMap(v => v.s);
				// 	const filtrados = dtJsonCryptos24H.filter( (d) =>  { return symbolos.filter(v => v==d.s ).length==0; })
				// 	console.log({previaData,symbolos,filtrados})

				// }
			});

			/* agregando listener a socket */

			webSocket.value.addEventListener("message", function (evt) {
				const dtJson = JSON.parse(evt.data);
				const socketTargetSymbol = dtJson.s;
				const socketTargetTemporality = dtJson.k.i;

				// si no se ha recibido el cuerpo o no el el simbolo en target no hase hace nada
				if (
					!dtJson.k ||
					socketTargetSymbol != symbols.value ||
					socketTargetTemporality != temporality.value
				) {
					return;
				}

				const xClosed = dtJson.k.x ? dtJson.k.x : false;
				const timeValue = dtJson.k.T ? parseInt(dtJson.k.T) : null;
				const volumen = dtJson.k.v ? parseFloat(dtJson.k.v) : null;

				if (
					candleSeries &&
					dtJson &&
					dtJson.s &&
					dtJson.k &&
					dtJson.k.c &&
					dtJson.s == symbols.value &&
					dtJson.k.i == temporality.value &&
					socketTargetTemporality == temporality.value
				) {
					cantidadSegundos.value = parseInt(
						(dtJson.k.T - dtJson.E) / 1000
					);
					const priceC = parseFloat(dtJson.k.c);

					priceRealtime.value =
						priceC > 50 ? parseFloat(priceC).toFixed(2) : priceC;

					if(store.getPriceOrder == 0 ){
						store.setPriceOrder(parseFloat(priceRealtime.value));
					}

					const objUpdate = {
						time: parseInt(dtJson.k.T),
						open: parseFloat(dtJson.k.o),
						high: parseFloat(dtJson.k.h),
						low: parseFloat(dtJson.k.l),
						close: priceC,
						volume: volumen,
						volumeq: volumen,
					};

					if (checkHeikenAshi.value) {
						const dataChart = {
							low: dataCandles.value.flatMap((d) => d.low),
							close: dataCandles.value.flatMap((d) => d.close),
							high: dataCandles.value.flatMap((d) => d.high),
							open: dataCandles.value.flatMap((d) => d.open),
							time: dataCandles.value.flatMap((d) => d.time),
							timestamp: dataCandles.value.flatMap((d) => d.time),
						};
						let lastCandleHA = calculoNextHeikinAskiCandle(
							dataChart,
							objUpdate
						);
						lastCandleHA.time = lastCandleHA.timestamp;
						candleSeries.update(lastCandleHA);
						store.setCandleLastPrice(lastCandleHA);
					} else {
						candleSeries.update(objUpdate);
						store.setCandleLastPrice(objUpdate);
					}
					//para el RSI
					updateRealtime(objUpdate, dtJson.s, dtJson.k.i);
					marcarPullback();

					// actualizar volumen en realtime
					if (volumeSeries) {
						// Volumen Color
						let styleVolumenColor =
							dtJson.k.c < dtJson.k.o
								? "rgba(255,82,82, 0.7)"
								: "rgba(25,245,235,0.7)";

						volumeSeries.update({
							value: volumen,
							time: timeValue,
							color: styleVolumenColor,
						});
					}
				}

				if (xClosed && socketTargetTemporality == temporality.value) {
					// actualizacion de data de chart VELAS para calculos
					let dataVela = {
						time: timeValue,
						open: parseFloat(dtJson.k.o),
						high: parseFloat(dtJson.k.h),
						low: parseFloat(dtJson.k.l),
						close: parseFloat(dtJson.k.c),
						volume: volumen,
						volumeq: volumen,
					};

					updateChartData(dataVela);
				}
			});

			webSocket.value.addEventListener("open", function (evt) {
				//console.log("* open", evt);
				socketOpen.value = true;
			});

			webSocket.value.addEventListener("close", function (evt) {
				// socketOpen.value = false;
				//console.log("* close", evt);
			});

			return;

			// ----------------------------------------------------------

			// console.log({socketOpen:socketOpen.value,socket,socketsCTR:socketsCTR.value})

			const existSocket = socketsLot.value.filter(
				(s) => s.symbol == socket
			);

			// aun no se ha subscrito a este par y el socket se encuentra abierto
			if (existSocket.length == 0 && socketOpen.value) {
				socketsCTR.value = socketsCTR.value + 1;
				const symbolsMinuscula = symbols.value.toString().toLowerCase();

				const msg = {
					method: "SUBSCRIBE",
					params: [`${symbolsMinuscula}@kline_${temporality.value}`],
					id: socketsCTR.value,
				};

				webSocket.value.send(JSON.stringify(msg));

				socketsLot.value.push({
					symbol: socket,
					nro: socketsCTR.value,
					temporality: temporality.value,
				});
			}

			// new WebSocket(`wss://fstream-auth.binance.com/stream?streams=${cadenaStreamsFutures}&listenKey=${getListenkey}`);

			/*
			const msg = {
			  "method": "SUBSCRIBE",
			  "params": [
			    "btcusdt@aggTrade",
			    "btcusdt@depth"
			  ],
			  "id": socketsCTR.value
			};
			webSocket.value.send(JSON.stringify(msg));
			------------ SUBSCRIBE
			{
			  "method": "SUBSCRIBE",
			  "params": [
			    "btcusdt@aggTrade",
			    "btcusdt@depth"
			  ],
			  "id": 1
			}

			---------- UNSUBSCRIBE
			{
				  "method": "UNSUBSCRIBE",
				  "params": [
				    "btcusdt@depth"
				  ],
				  "id": 312
			}
			*/
		}, 1000);

		onMounted(async () => {
			// Busqueda de datos
			await searchData();
			await startingChart();

			// Events -----

			window.addEventListener("resize", () => {
				resiseChart();
			});

			const searchAndConvinePreviusData = debounce(async function () {
				loading.value = true;
				const dataPrimeraBarra = dataCandles.value.shift();
				const array1 = dataCandles.value;
				const respBusqueda = await searchDataChart(
					symbols.value,
					temporality.value,
					700,
					dataPrimeraBarra.time
				);

				// antes const array2 = await formatKlineToCandlesFormat(respBusqueda);
				const array2 = await procesarData(respBusqueda);

				let final = [];

				// la data nueva
				array2.map((d) => {
					final.push(d);
				});

				// la data ya cargada anteriormente
				array1.map((d) => {
					final.push(d);
				});

				dataCandles.value = final;
				store.setDataCandles(final);
				candleSeries.setData(final);
				updateVolumen();
				//candleSeries.setData(dataCandles.value);

				procesarMarcaciones(final);
				aplicarMarcacionesActivas();
				marcarVelasHeikinAshi();
				atrUpdate();
				psarUpdate();
				loading.value = false;
			}, 1500);

			async function onVisibleTimeRangeChanged(newVisibleTimeRange) {
				let infoChart = await chart
					.timeScale()
					.getVisibleLogicalRange();
				const primeraBarra = parseInt(infoChart.from);

				store.setUpdateChartMain({ primeraBarra, infoChart});

				if (primeraBarra <= 0) {
					// hace el request y a la data ya cargada agrega el historial de la req
					searchAndConvinePreviusData();
				}

				const fecha = new Date().toLocaleString();
				chart.applyOptions({
					watermark: {
						color: "#00ff11", // "rgba(10, 194, 129, 0.8)",
						visible: true,
						text: `Topacio - (${symbols.value} - ${temporality.value}) - Alpha minimal  vr: ${primeraBarra} - ${fecha}`,
						fontSize: 14,
						horzAlign: "left",
						vertAlign: "top",
					},
				});

				/*
					* ejemplo cuando hay que actualizar multiples charts

				    var barSpacing1 = chart.timeScale().getBarSpacing();
				    var scrollPosition1 = chart.timeScale().scrollPosition();
				    chart2.timeScale().applyOptions({rightOffset: scrollPosition1,barSpacing: barSpacing1})
				*/
			}

			chart
				.timeScale()
				.subscribeVisibleTimeRangeChange(onVisibleTimeRangeChanged);


			// test cursor
			// chart
			// 	.timeScale()
			// 	.subscribeVisibleLogicalRangeChange( syncHandler)
			
			// //scrollPosition
			// //scrollToPosition
			// //scrollToRealTime

			// function syncHandler(e) {
			//     //var barSpacing1 = chart.timeScale().getBarSpacing();
			//     //var scrollPosition1 = chart.timeScale().scrollPosition();
			//     console.log('syncHandler',chart.timeScale(),e)
			//     // chart2.timeScale().applyOptions({rightOffset: scrollPosition1,barSpacing: barSpacing1})
			// }
			
			
			document.getElementById('chart-graphic')
			.addEventListener('mousemove', (event) => {

			    // event.getBarSpacing = chart.timeScale().getBarSpacing();
			    // event.scrollPosition = chart.timeScale().scrollPosition();
			    // console.log('chart.timeScale()',chart.timeScale().scrollPosition(),chart.timeScale())
			    
			    event.infoChart = chart
					.timeScale()
					.getVisibleLogicalRange();
				
				event.primeraBarra = parseInt(event.infoChart.from);


			});


			// // testing
			// chart.subscribeCrosshairMove(crosssyncHandler);
			// function crosssyncHandler(e) {
			//   if (e.time !== undefined) {
			//     var xx = stocastic.value.containerChartStocastic.timeScale().timeToCoordinate(e.time);
			//     stocastic.value.setCrossHairXY(xx,50,true);
			//   } else if (e.point !== undefined){
			//     stocastic.value.containerChartStocastic.setCrossHairXY(e.point.x,10,false);
			//   }
			// }


			candleSeries.createPriceLine({
				price: 17000,
				color: 'rgba(53, 162, 74, 1)',
				lineWidth: 2,
				lineStyle: 2,// LightweightCharts.LineStyle.Dotted(2),
			  	title: 'buy order',
				draggable: true,
			});


			function handlerDragger(params) {
				const line = params.customPriceLine;
					
					const Toast = Swal.mixin({
					          toast: true,
					          position: "top-end",
					          showConfirmButton: false,
					          timer: 3000,
					          timerProgressBar: true,
					          didOpen: (toast) => {
					            toast.addEventListener("mouseenter", Swal.stopTimer);
					            toast.addEventListener("mouseleave", Swal.resumeTimer);
					          },
					    });

			        Toast.fire({
					          icon: "success",
					          title: `${line.options().title} actualizada a ${chart.priceScale('right').formatPrice(line.options().price)}`,
					        });
			        //`${line.options().title} dragged from ${params.fromPriceString} to ${chart.priceScale('right').formatPrice(line.options().price)}`

			}

			chart.subscribeCustomPriceLineDragged(handlerDragger);




			aplicarMarcacionesActivas();
		});

		onUnmounted(() => {
			if (chart) {
				//chart.remove();
				//chart = null;
			}
		});

		const changeTarget = async (target) => {
			loading.value = true;
			cantidadSegundos.value = null;
			priceRealtime.value = null;
			rsiLast.value = null;
			store.setPriceOrder(0);
			chart.unsubscribeCrosshairMove(tooltipsController);

			symbols.value = target.value;
			const r = await searchData();
			//await updateChart();
			candleSeries.setData(r);
			updateVolumen();
			aplicarMarcacionesActivas();
			chart.subscribeCrosshairMove(tooltipsController);
			marcarVelasHeikinAshi();
			atrUpdate();
			psarUpdate();
			resetZoom();
			await nextTick();
			loading.value = false;
		};

		return {
			getClandlesFormat,
			takeScreen,
			captureScreen,
			rsiLast,
			priceRealtime,
			checkPullBack,
			checkHeikenAshi,
			activadorEMA10,
			activadorEMA21,
			activadorEMA55,
			activadorEMA100,
			activadorEMA200,
			activadorEMA500,
			activadorFA,
			eventEMA10,
			eventEMA21,
			eventEMA55,
			eventEMA100,
			eventEMA200,
			eventEMA500,
			targetSymbol,
			targetTemporality,
			dataTemporality,
			changeTemporality,
			changeTarget,
			dataSymbols,
			datos,
			dataCandles,
			loading,
			temporality,
			symbols,
			store,
			rsiMax,
			rsiMin,
			activadorBB,
			activadorDT,
			eventBB,
			eventDT,
			limpiarMarcacionesFibonacci,
			fibonacciAuto,
			cantidadSegundos,
			checkATR,
			checkPSAR,
			stocastic,
			p24h,
			irAFecha,
			fechaTarget,
			isAgresive,
			showModalDate
		};
	},
};
</script>

