	display: block;
	position: absolute;
	top: 0;
	left: 0;
	width: inherit;
	height: inherit;
	border-radius: 50%;
	transform: rotateX(70deg);
	animation: 1s spin linear infinite;
}
.loader:after {
	color: #1fefa3; /*#ff3d00;*/
	transform: rotateY(70deg);
	animation-delay: 0.4s;
}

.loader-chart {
