<template>
	<div class="wrapper-section">
		<div class="container-agresive">
			<div class="Micro">
				<ChartMicroTemporality :mricrotemp="5000" :title="'5 Seg'" :symbol="symbol"/>
				<ChartMicroTemporality :mricrotemp="20000" :title="'20 Seg'" :symbol="symbol"/>
			</div>
			<div class="Main">
				<div id="chart-main-agresive"></div>
				<ChartStocastic
					v-if="dataCandles"
					ref="stocastic"
				></ChartStocastic>
			</div>
		</div>
	</div>
</template>
<script>
import useCharts from "../composables/useCharts";
import { mainStore } from "../store/mainStore";
import ChartStocastic from "../components/ChartStocastic.vue";
import ChartMicroTemporality from "./micro/ChartMicroTemporality.vue";

import {
	defineComponent,
	ref,
	reactive,
	onMounted,
	watch,
	nextTick,
	onUnmounted,
	computed,
	toRefs,
} from "vue";
const {
	searchDataChart,
	formatKlineToCandlesFormat,
	configVelasTransparentes,
	configChart,
	resizeWebBrowser,
	calcularVWAP,
	calculoRSI,
	calculatePercentageBetweenValues,
	calculateBollingerBands,
	calculateEMA,
	carcularRetrocesoFibonacci,
	calcularSMA,
	calculoVelasHeikinAshi,
	calculoNextHeikinAskiCandle,
	calculeATR,
	calculoPSAR,
	isDoji,
	isBullishspinningtop,
	isBearishspinningtop,
	Chandelier,
	configLine1,
	configLine2,
} = useCharts();

export default {
	name: "AgresivePanel",
	components: {
		ChartStocastic,
		ChartMicroTemporality,
	},
	props:["symbol"],
	setup(props) {
		const store = mainStore();
		const { symbol } = toRefs(props);

		const dataCandles = ref(false);
		let chart;
		let candleSeries;
		const stocastic = ref([]);
		const startingChart = async () => {
			if (store.dataCandles && store.dataCandles.data) {
				dataCandles.value = JSON.stringify(
					store.dataCandles.data.map((d) => d)
				);

				// data con el Formato de las candles
				dataCandles.value = JSON.parse(dataCandles.value);

				var configChartCustom = configChart;
			}

			watch(
				() => store.getDataCandles,
				function (e) {
					// console.log('value changes dataCandles',e);
					dataCandles.value = JSON.parse(
						JSON.stringify(store.getDataCandles)
					);

					updateData();
				}
			);

			watch(
				() => store.getCandleLastPrice,
				function (candleLastPrice) {
					if (candleSeries) {
						candleSeries.update(candleLastPrice);
					}
				}
			);

			watch(
				symbol,
				function (s) {
					const fecha = new Date().toLocaleString();
					console.log({symbol})
					chart.applyOptions({
						watermark: {
							color: "#00ff11", // "rgba(10, 194, 129, 0.8)",
							visible: true,
							text: `Topacio - (${s} ${store.getTemporalityTarget} - ${fecha})`,
							fontSize: 14,
							horzAlign: "left",
							vertAlign: "bottom",
						},
					});
				}
			);

			watch(
				() => store.getTemporalityTarget,
				function (t) {
					const fecha = new Date().toLocaleString();

					chart.applyOptions({
						watermark: {
							color: "#00ff11", // "rgba(10, 194, 129, 0.8)",
							visible: true,
							text: `Topacio - (${store.getTargetSymbol} ${t} - ${fecha})`,
							fontSize: 14,
							horzAlign: "left",
							vertAlign: "bottom",
						},
					});
				}
			);

			const updateData = async () => {
				candleSeries.setData(dataCandles.value);
			};

			onMounted(async () => {
				// init chart & setting config
				configChartCustom.height = parseInt(
					document.getElementById("chart-main-agresive").clientHeight
				);
				document.getElementById("chart-main-agresive"),
					(configChartCustom.width = parseInt(
						document.getElementById("chart-main-agresive")
							.clientWidth
					));

				chart = LightweightCharts.createChart(
					document.getElementById("chart-main-agresive"),
					configChartCustom
				);

				candleSeries = chart.addCandlestickSeries(
					configVelasTransparentes
				);
				candleSeries.setData(dataCandles.value);

				const fecha = new Date().toLocaleString();
				// - ${temporality.value}) - Alpha minimal  vr: ${primeraBarra}
				chart.applyOptions({
					watermark: {
						color: "#00ff11", // "rgba(10, 194, 129, 0.8)",
						visible: true,
						text: `Topacio - (${store.getTargetSymbol} ${store.getTemporalityTarget}- ${fecha})`,
						fontSize: 14,
						horzAlign: "left",
						vertAlign: "bottom",
					},
				});

				// Events Chart
				//stocastic.value.updateCursor(event);
				chart.timeScale().subscribeSizeChange((s) => {
					console.log("SizeChange", s);
				});

				chart
					.timeScale()
					.subscribeVisibleLogicalRangeChange((logicalRange) => {
						console.log(
							"VisibleLogicalRangeChange",
							logicalRange,
							stocastic.value.diferenciaStochastic
						);

						// actualizando Estocastico
						stocastic.value.containerChartStocastic
							.timeScale()
							.setVisibleLogicalRange(logicalRange);
						// organizando a margen
						// stocastic.value.containerChartStocastic
						// .timeScale().fitContent();

						// stocastic.value.updateCursor(event);
						//.timeScale().timeToCoordinate(e.time);
						//.timeScale().scrollToPosition(dt.layerX,dt.layerY)
					});

				chart.timeScale().subscribeVisibleTimeRangeChange((s) => {
					console.log("VisibleTimeRangeChange", s);
				});
			});
		};

		startingChart();
		return {
			dataCandles,
			stocastic,
		};
	},
};
</script>
