<template>
	<div :id="`chart-micro-${mricrotemp}`" class="chat-micro"></div>
</template>
<script>
import {
	ref,
	reactive,
	onMounted,
	toRefs,
	watch,
	computed,
	getCurrentInstance,
	nextTick,
} from "vue";
import useCharts from "../../composables/useCharts";
const {
	searchDataChart,
	formatKlineToCandlesFormat,
	configVelasTransparentes,
	configChart,
	resizeWebBrowser,
	calcularVWAP,
	calculoRSI,
	calculatePercentageBetweenValues,
	calculateBollingerBands,
	calculateEMA,
	carcularRetrocesoFibonacci,
	calcularSMA,
	calculoVelasHeikinAshi,
	calculoNextHeikinAskiCandle,
	calculeATR,
	calculoPSAR,
	isDoji,
	isBullishspinningtop,
	isBearishspinningtop,
	Chandelier,
	configLine1,
	configLine2,
} = useCharts();

import { mainStore } from "../../store/mainStore";

export default {
	name: "ChartMicroTemporality",
	props: ["mricrotemp", "title", "symbol"],
	setup(props) {
		const store = mainStore();
		const { mricrotemp, title, symbol } = toRefs(props);
		let fechaFin = new Date().getTime() + mricrotemp.value;
		let chart;
		let candleSeries;
		const configChartCustom = configChart;

		const lastCandle = reactive({
			time: parseInt(fechaFin),
			open: parseFloat(store.getCandleLastPrice.close),
			high: parseFloat(store.getCandleLastPrice.close),
			low: parseFloat(store.getCandleLastPrice.close),
			close: parseFloat(store.getCandleLastPrice.close),
			volume: store.getCandleLastPrice.volume,
			volumeq: store.getCandleLastPrice.volumeq,
		});

		let controlHigh = store.getCandleLastPrice.close;
		let controlClose = store.getCandleLastPrice.close;
		let controlLow = store.getCandleLastPrice.close;
		let controlOpen = store.getCandleLastPrice.close;

		// Cambio de simbolo en target
		watch(symbol, (s) => {
			//limpieza
			candleSeries.setData([]);
			const fecha = new Date().toLocaleString();			
			chart.applyOptions({
				watermark: {
					color: "#00ff11", // "rgba(10, 194, 129, 0.8)",
					visible: true,
					text: `Topacio - ${s} ${title.value}`,
					fontSize: 14,
					horzAlign: "left",
					vertAlign: "top",
				},
			});
		});

		watch(
			() => store.getCandleLastPrice,
			function (candleLastPrice) {
				let fechaCurrent = new Date().getTime();

				if (fechaCurrent > fechaFin) {
					fechaFin += mricrotemp.value;
					candleLastPrice.open = candleLastPrice.close;
					controlHigh = candleLastPrice.close;
					controlOpen = candleLastPrice.close;
					controlLow = candleLastPrice.close;
				}

				controlClose = candleLastPrice.close;

				if (candleLastPrice.close > controlHigh) {
					controlHigh = candleLastPrice.close;
				}

				if (candleLastPrice.close < controlLow) {
					controlLow = candleLastPrice.close;
				}

				if (candleSeries) {
					candleSeries.update({
						time: parseInt(fechaFin),
						open: parseFloat(controlOpen),
						high: parseFloat(controlHigh),
						low: parseFloat(controlLow),
						close: parseFloat(controlClose),
						volume: store.getCandleLastPrice.volume,
						volumeq: store.getCandleLastPrice.volumeq,
					});
				}
			}
		);

		onMounted(async () => {
			const targetChart = document.getElementById(
				`chart-micro-${mricrotemp.value}`
			);

			configChartCustom.height = parseInt(targetChart.clientHeight);
			configChartCustom.width = parseInt(targetChart.clientWidth);

			chart = LightweightCharts.createChart(
				targetChart,
				configChartCustom
			);

			candleSeries = chart.addCandlestickSeries(configVelasTransparentes);

			candleSeries.setData([lastCandle]);

			chart.applyOptions({
				watermark: {
					color: "#00ff11", // "rgba(10, 194, 129, 0.8)",
					visible: true,
					text: `Topacio - ${store.getTargetSymbol} - ${title.value}`,
					fontSize: 14,
					horzAlign: "left",
					vertAlign: "top",
				},
			});
		});
	},
};
</script>
<style scoped>
	div.chat-micro{
		background-color: rgba(0, 0, 0, 0.45);
	}
</style>