<script>
import {
	ref,
	reactive,
	onMounted,
	toRefs,
	watch,
	computed,
	getCurrentInstance,
	nextTick,
} from "vue";
import useCharts from "../composables/useCharts";
const { calculoStochasticRSI, configLine1, configLine2, calculateKST } =
	useCharts();

import { mainStore } from "../store/mainStore";

import {
	createChart,
	CrosshairMode,
	PriceScaleMode,
	isBusinessDay,
} from "lightweight-charts";

export default {
	name: "ChartStocastic",
	//props: ["datos"],
	setup(props, { emit, parent }) {
		const store = mainStore();

		//const { datos } = toRefs(props);
		const datos = [];

		const dataFormato = ref(
			JSON.parse(JSON.stringify(store.getDataCandles))
		);

		const dataJSON = JSON.parse(JSON.stringify(store.getDataCandles));

		const originalTime = ref(dataJSON.flatMap((d) => d.time));

		// const instance = getCurrentInstance();
		// console.log("parent:",instance.parent);

		const lineStocastic1 = ref(0);
		const lineStocastic2 = ref(0);
		const lineStocasticC = ref(0);

		const line1KST = ref(0);
		const line2KST = ref(0);
		const KST = ref([]);

		const containerChartStocastic = ref(0);

		const diferenciaStochastic = ref(0);

		// watch(datos, (newValue) => {
		// 	updateData(newValue);
		// });

		// watch(updatemain, (newValue) => {
		// 	console.log('updatemain',newValue)
		// });

		// watch(store.getDataCandles, (newValue) => {
		// 	console.log('getDataCandles',newValue)
		// });

		watch(
			() => store.getDataCandles,
			function (e) {
				// console.log('value changes dataCandles',e);
				dataFormato.value = JSON.parse(
					JSON.stringify(store.getDataCandles)
				);
				updateData();
			}
		);

		watch(
			() => store.getUpdateChartMain,
			function (e) {
				containerChartStocastic.value
					.timeScale()
					.setVisibleLogicalRange({
						from: e.infoChart.from,
						to: e.infoChart.to,
					});
			}
		);

		onMounted(async () => {
			const closeValues = store.getDataCandles.flatMap((d) => d.close);
			const timeValues = store.getDataCandles.flatMap((d) => d.time);
			KST.value = await calculateKST(closeValues);

			console.log({ KST, closeValues });

			// datos
			const stocasticoRSIH = await calculoStochasticRSI(
				dataFormato.value,
				"high"
			);

			const stocasticoRSIL = await calculoStochasticRSI(
				dataFormato.value,
				"low"
			);
			const stocasticoRSI = await calculoStochasticRSI(
				dataFormato.value,
				"close"
			);

			const ancho = document.getElementById("chart-main-agresive")
				? document.getElementById("chart-main-agresive").clientWidth
				: document.getElementById("chart-graphic").clientWidth;

			const alto = document.getElementById("chart-main-agresive")
				? document.getElementById("chart-main-agresive").clientHeight /
				  4
				: document.getElementById("chart-graphic").clientHeight / 4;

			// ======== chart Stocastic =========
			containerChartStocastic.value = createChart(
				document.getElementById("chart-stocastic"),
				{
					width: parseInt(ancho),
					height: parseInt(100),
					lastValueVisible: false,
					aseLineVisible: false,
					scaleMargins: {
						top: 0.2,
						bottom: 0.2,
					},
					handleScroll: {
						pressedMouseMove: false, // evitar zoom con el mouse
					},
					handleScale: {
						mouseWheel: false, // evitar scalado
					},
					layout: {
						visible: false,
						layoutVisible: false,
						textColor: "#fff",
						backgroundColor: "transparent", // '#000000',
					},
					grid: {
						vertLines: {
							color: "transparent",
						},
						horzLines: {
							color: "transparent",
						},
					},
					rightPriceScale: {
						borderColor: "rgba(255, 255, 255, 1)",
						visible: true,
					},
					leftPriceScale: {
						borderColor: "rgba(255, 255, 255, 1)",
						visible: false,
					},
					timeScale: {
						visible: false,
						timeVisible: false,
						secondsVisible: false,
					},
					crosshair: {
						mode: 0,
						vertLine: {
							visible: true,
							labelVisible: false,
							color: "rgba(255, 255, 255, 1)",
						},
						horzLine: {
							visible: true,
							labelVisible: false,
							color: "rgba(255, 255, 255, 1)",
						},
					},
					localization: {
						priceFormatter: (price) => {
							return parseFloat(price).toFixed(2);

							let cantidad_formato = parseFloat(price).toFixed(2);
							let restantes = 16 - cantidad_formato.length;

							while (restantes > 0) {
								cantidad_formato = cantidad_formato + " ";
								restantes--;
							}

							return cantidad_formato;
						},
					},
					watermark: {
						color: "#fff",
						visible: true,
						text: `Stocastic RSI`,
						fontSize: 14,
						horzAlign: "left", //right 'left',
						vertAlign: "center", //'bottom top',
					},
				}
			);

			lineStocastic1.value =
				containerChartStocastic.value.addLineSeries(configLine1);
			lineStocastic2.value =
				containerChartStocastic.value.addLineSeries(configLine2);
			lineStocasticC.value = containerChartStocastic.value.addLineSeries({
				lineWidth: 1,
				color: "#fff",
				lineStyle: 2,
				axisLabelVisible: false,
				priceLineVisible: false,
				lastValueVisible: false,
			});

			const l1_d = stocasticoRSI.flatMap((s) =>
				parseFloat(parseFloat(s.d).toFixed(2))
			);
			const l1_k = stocasticoRSI.flatMap((s) =>
				parseFloat(parseFloat(s.k).toFixed(2))
			);
			const l1_stochRSI = stocasticoRSI.flatMap((s) => s.stochRSI);

			const l2_d = stocasticoRSIL.flatMap((s) =>
				parseFloat(parseFloat(s.d).toFixed(2))
			);
			const l2_k = stocasticoRSIL.flatMap((s) =>
				parseFloat(parseFloat(s.k).toFixed(2))
			);
			const l2_stochRSI = stocasticoRSIL.flatMap((s) => s.stochRSI);

			diferenciaStochastic.value =
				dataFormato.value.length - stocasticoRSIH.length;

			let formatoStocasticoH = [];
			let formatoStocasticoL = [];
			let formatoStocasticoCONTROL = [];

			for (var i = dataFormato.value.length - 1; i >= 0; i--) {
				const ultimo_D = l1_d.pop();
				const ultimo_C = l1_stochRSI.pop();
				const ultimo_K = l1_k.pop();
				const timeOriginal = originalTime.value.pop();

				if (!timeOriginal) {
					return;
				}

				formatoStocasticoH.push({
					time: timeOriginal, //time,
					value: ultimo_D,
				});

				formatoStocasticoL.push({
					time: timeOriginal, //time,
					value: ultimo_K,
				});

				formatoStocasticoCONTROL.push({
					time: timeOriginal, //time,
					value: ultimo_C,
				});
			}

			formatoStocasticoH.reverse();
			formatoStocasticoL.reverse();
			formatoStocasticoCONTROL.reverse();
			// seteo de data
			lineStocastic1.value.setData(formatoStocasticoH);
			lineStocastic2.value.setData(formatoStocasticoL);
			// lineStocasticC.value.setData(formatoStocasticoCONTROL);

			const maxPriceLine = {
				price: 85,
				color: "red",
				lineWidth: 1,
				lineStyle: 2, // LineStyle.Dashed
				axisLabelVisible: false,
				title: "max",
			};
			const minPriceLine = {
				price: 15,
				color: "green",
				lineWidth: 1,
				lineStyle: 2, // LineStyle.Dashed
				axisLabelVisible: false,
				title: "min",
			};

			lineStocastic1.value.createPriceLine(minPriceLine);
			lineStocastic1.value.createPriceLine(maxPriceLine);

			setTimeout(() => {
				containerChartStocastic.value.priceScale().applyOptions({
					autoScale: false,
					timeScale: {
						rightOffset: 5, // pegado al lateral
						barSpacing: 4, // zoom  en scroll interno lateral en (X)
						// lockVisibleTimeRangeOnResize: true,
						// rightBarStaysOnScroll: true,
						// visible: true,
						// timeVisible: true,
						// secondsVisible: false,
					},
				});
			}, 500);
		});

		const updateData = async () => {
			const dataFormato = JSON.parse(
				JSON.stringify(store.getDataCandles)
			);

			const stocasticoRSI = await calculoStochasticRSI(
				dataFormato,
				"close"
			);

			originalTime.value = dataFormato.flatMap((d) => d.time);

			const l1_d = stocasticoRSI.flatMap((s) =>
				parseFloat(parseFloat(s.d).toFixed(2))
			);
			const l1_k = stocasticoRSI.flatMap((s) =>
				parseFloat(parseFloat(s.k).toFixed(2))
			);
			const l1_stochRSI = stocasticoRSI.flatMap((s) => s.stochRSI);

			let formatoStocasticoH = [];
			let formatoStocasticoL = [];
			let formatoStocasticoCONTROL = [];

			const dataJSON = JSON.parse(JSON.stringify(store.getDataCandles));

			for (var i = l1_d.length; i >= 0; i--) {
				const ultimo_D = l1_d.pop();
				const ultimo_C = l1_stochRSI.pop();
				const ultimo_K = l1_k.pop();

				const lastTime = originalTime.value.pop();
				console.log({ lastTime });
				if (!ultimo_D || !lastTime) {
					return;
				}

				formatoStocasticoH.push({
					time: lastTime,
					value: ultimo_D,
				});

				formatoStocasticoL.push({
					time: lastTime,
					value: ultimo_K,
				});

				formatoStocasticoCONTROL.push({
					time: lastTime,
					value: ultimo_C,
				});
			}

			// ordenando
			formatoStocasticoH.reverse();
			formatoStocasticoL.reverse();
			formatoStocasticoCONTROL.reverse();

			// seteo de data
			lineStocastic1.value.setData(formatoStocasticoH);
			lineStocastic2.value.setData(formatoStocasticoL);
		};

		const updateCursor = (dt) => {
			// var xx = containerChartStocastic.timeScale().timeToCoordinate(e.time);
			// containerChartStocastic.setCrossHairXY(dt.clientX,200,true);
			//candlestickSeries1.priceToCoordinate(candlestickSeries4.coordinateToPrice(e.point.y));
			//containerChartStocastic.value.priceToCoordinate(candlestickSeries4.coordinateToPrice(e.point.y));
			// mueve el scroll Horizontalmente containerChartStocastic.value.timeScale().scrollToPosition(dt.layerX,dt.layerY)
			// containerChartStocastic.value.timeScale().timeToCoordinate(40)
			// console.log('updateCursor',
			// 	// containerChartStocastic.value.priceScale(),
			// 	//containerChartStocastic.value.priceScale().scrollPosition,
			// 	// containerChartStocastic.value.priceScale().scrollToRealTime(),
			// 	//containerChartStocastic.value.priceScale().g(),
			// 	containerChartStocastic.value.timeScale().scrollPosition(),
			// 	//containerChartStocastic.value,
			// 	{
			// 		x:dt.clientX,
			// 		y:dt.clientY,
			// 		lx:dt.layerX,
			// 		ly:dt.layerY,
			// 		dt
			// 	});
		};

		// 		// estilo y colores
		// containerChartStocastic.value.applyOptions({
		//     crosshair: {
		//         // Change mode from default 'magnet' to 'normal'.
		//         // Allows the crosshair to move freely without snapping to datapoints
		//         mode: 0,//LightweightCharts.CrosshairMode.Normal,

		//         // Vertical crosshair line (showing Date in Label)
		//         vertLine: {
		//             width: 8,
		//             color: '#C3BCDB44',
		//             style: 0,//LightweightCharts.LineStyle.Solid,
		//             labelBackgroundColor: '#9B7DFF',
		//         },

		//         // Horizontal crosshair line (showing Price in Label)
		//         horzLine: {
		//             color: '#fff',
		//             labelBackgroundColor: '#9B7DFF',
		//         },
		//     },
		// });

		return { updateCursor, containerChartStocastic, diferenciaStochastic };
	},
};
</script>
