<style scoped>

a.linkedin svg{
    color: var(--color-text-bibrant);
    background-color: var(--color-bibrant);
    transition: all 0.5s;
}

.container-cookies {
	background-color: #1d262bb0;
	color: #fff;
	padding: 0.7em;
	margin: 0 auto;
	border-radius: 7px;
	/*height: 50px;*/
	font-weight: 300;
	display: flex;
	justify-content: space-between;
	height: auto;
	width: 80%;
	position: absolute;
    margin-left: 25%;
}

.container-cookies > button {
	font-size: 0.8em;
	max-height: 30px;
	min-height: 30px;
	margin: 0px;
}
.container-cookies > span {
	display: flex;
	justify-content: space-between;
	margin: 0 auto;
}
.container-cookies > span > img {
	margin: 0 5px;
}

/*cuando tiene resolucion con menos de 600px*/
@media only screen and (max-width: 800px) {
	.container-cookies {
		width: 77%;
	}
}

@media only screen and (min-width: 1000px) {
	.container-cookies {
		width: 50%;
	}
}
</style>
<script>
import { ref } from "vue";
export default {
	setup() {
	    const cookies = ref(false);

		if (localStorage && localStorage.getItem("topacio-cookie-accept")) {
			cookies.value = (localStorage.getItem("topacio-cookie-accept")=='true')?true:false
		}

		const acceptCookies = () =>{
			localStorage.setItem("topacio-cookie-accept",true);
			cookies.value = true;
		}

		return {acceptCookies,cookies};

	},
};
</script>
