const useIndexDB = () => {

	const indexDB = window.indexedDB || window.mozIndexedDB || window.webkitIndexedDB || window.msIndexedDB;
	let dataBase;
	let active;


	const startBD = async () => {
	 	dataBase = await indexDB.open('topacio-trade',1);
		
		dataBase.onupgradeneeded=( e )=>{
			// indexDB.createTable('config');
			active = dataBase.result;

			const obj = active.createObjectStore('config',{ keyPath:'id', autoIncrement:true });
				  obj.createIndex('by_name','name',{ unique:true });
				  obj.createIndex('by_data','data',{ unique:false });
			


			console.log('onupgradeneeded',{e})
		};

		dataBase.onsuccess=( e )=>{
			active = dataBase.result;
			console.log('onsuccess',{e})
		};

		dataBase.onerror=( e )=>{
			console.log('onerror',{e})
		};
		
	}



	const addConfig = async () => {
	 	console.log({dataBase,active})
		// const active = dataBase.result;
	 	
		let data = active.transaction(['config'],"readwrite");
		let obj  = data.objectStore("config");

		let request = obj.put({
			name: "algodon",
			data: "aksjdhsa98d79sa8duasjd"
		});

		request.oncomplete=( e )=>{
			console.log('oncomplete request',{e})
		};

		request.onerror=( e )=>{
			console.log('onerror request',{e})
		};
	}
	

	return {
		indexDB,
		startBD,
		addConfig,
		dataBase,
	};
};

export default useIndexDB;
