<template>
	<div
		id="container-event"
		class="w-full min-h-[calc(100vh-180px)] flex items-center justify-center"
		v-if="store.inWhiteList()"
	>
		<div class="text-center mt-[-20vh] md:mt-0">
			<main class="parent-content-event">
				<div class="card lateral-l">
					<div class="content">
						<h2 class="title">Fragmentos Genesis</h2>
						<p class="copy">Evento del Sorteo al Mint Genesis</p>
						<!-- /img/galeria/img-5.png -->
						<img
							src="/img/topacio_azul.svg"
							alt=""
							id="logo-topacio"
							class="profile jv mini-logo"
						/>
						<button
							class="btn btn-sorteo-register"
							@click="store.participarEnSorteo()"
						>
							Participar en Sorteo
						</button>
						<p class="copy">{{ store.costoParticipacionSorteo }}</p>
					</div>
				</div>

				<div class="details-center container-details">
					<article>
						Detalle de Todos los fragmentos liberados durante el
						evento
					</article>
					<ul class="container-list-winners">
						<li
							v-for="winner in store.hashesWinners.data"
							v-show="winner.img"
						>
							<label class="text-nft">
								NFT #{{ winner.nftId }} -
								{{ formatWallet(winner.wallet) }}
								({{
									new Date(
										winner.timestamp * 1000
									).toLocaleString()
								}})

								<p v-if="winner.attributes">
									{{ getAtributesFragment(winner) }}
								</p>
							</label>
							<a 
								target="_blank" 
								:href="`${store.getURLBlockchainScan()}/token/${store.getContractNFTS()}?a=${winner.nftId}`">
								<img
									v-if="winner.img"
									:src="winner.img"
									alt="image nft"
								/>
							</a>
						</li>
					</ul>
				</div>
				<div class="lateral-r">
					<article>Resumen Diario</article>
					<div class="container">
						<ul>
							<li>
								Cantidad Participaciones:
								{{ cantidadParticipantes() }}
							</li>
							<li>
								Fragmentos Liberados:
								{{ store.numeroSorteo - 1 }}
							</li>
							<li v-if="store.proximoSorteo > 0">
								<label class="lbl-resumen subtitle"
									>Sorteo Fecha</label
								>
								<label class="lbl-resumen">
									{{
										new Date(
											store.proximoSorteo * 1000
										).toLocaleString()
									}}
								</label>

								<CountDown
									:totalSegunds="cantidadSegundosSorteo()"
									titleCustom="Tiempo para el sorteo"
								></CountDown>
							</li>
							<li class="contenedor-detalle-participantes">
								<label>Participantes</label>
								<label
									v-for="participante in store.sorteoParticipantsInDay"
									class="lbl-participante"
									>
								    <img class="author-img avatar mini" :src="`https://avatars.dicebear.com/api/identicon/${participante}.svg?background=%23000`" alt="avatar">
									{{ formatWallet(participante) }}</label
								>
							</li>
						</ul>
					</div>
				</div>
			</main>
		</div>
	</div>

	<div
		v-else
		id="container-subscription"
		class="w-full min-h-[calc(100vh-180px)] flex items-center justify-center"
	>
		<div class="text-center mt-[-20vh] md:mt-0">
			<div class="flex justify-center" style="flex-direction: column">
				<h1 class="text-white">Yoy need conect</h1>
				<br />
				<p class="text-white">
					And use Blockchain {{ store.titleBlockchain }}
				</p>
				<p class="text-white">And added in white-list</p>
			</div>
		</div>
	</div>
</template>
<script>
import CountDown from "../components/CountDown.vue";

import { mainStore } from "../store/mainStore";
import { onBeforeMount } from "vue";


export default {
	name: "EventPage",
	components: {
		CountDown,
	},
	beforeCreate(){
		console.log('have been initialized.');
	},
  	created() {
    	console.log('will update.')
    },
	setup() {
		
		console.log('setup');
		const store = mainStore();

		const cantidadParticipantes = () =>{
			return (store?.sorteoParticipantsInDay?.length) ? store.sorteoParticipantsInDay.length:0;
		}

		const cantidadSegundosSorteo = () => {

			return (store && store.proximoSorteo) ?
					parseInt(( ( parseInt(store.proximoSorteo)*1000 ) - new Date().getTime() ) /1000) : 0;
		}

		const formatWallet = (w) =>{
			return `${w.substr(0,7)}  . . .  ${w.substr(-7,7)}`;
		}


        const getAtributesFragment = (meta)=>{

               if(!meta||!meta.attributes){
                    return ``;
               }

               const cadena = meta.attributes.flatMap( (v,i) => `${v.trait_type}:${v.value}\n` ).toString().replaceAll(',','');

               return `${cadena}`;
          };

		onBeforeMount(async()=>{
			await store.searchWinners();
		});

		return { store, cantidadParticipantes,formatWallet,cantidadSegundosSorteo,getAtributesFragment };
	},
};
</script>

